const QUOTE = "â€œ";
const createQuoteElement = () => {
  const quote = document.createElement("p");
  quote.innerHTML = QUOTE;

  return quote;
};

const replaceQuote = () => {
  document.querySelectorAll(".kl-testimonial__quote").forEach(wrapper => {
    debugger;
    const svg = wrapper.querySelector("svg");
    if (svg) {
      svg.classList.add("hidden");
    }

    wrapper.appendChild(createQuoteElement);
  });
};

export default replaceQuote;
